<script>

export default {
  name: 'waxom-video-presentation',
  data() {
    return{
      videoPlaying: 'unplayed'
    }
  },
  methods: {
    playVideo() {
      this.$refs.video.play();
      this.videoPlaying = 'played';
    },
    pauseVideo() {
      if (this.videoPlaying == 'paused') {
        this.$refs.video.play();
        this.videoPlaying = 'played';
      } else if (this.videoPlaying == 'played'){ 
        this.$refs.video.pause();
        this.videoPlaying = 'paused';
       }
    }
  },
  created() {
  }
  }
</script>

